<template>
  <div>
    <div class="member-select">
      <div class="member-list">
        <ul>
          <li @click="select(member.id)" v-for="(member, index) in memberList" :key="index">{{member.name}}</li>
        </ul>
      </div>
      <div class="memebr-selected">
        <ul>
          <li v-for="(member, index) in memberSelected" :key="index">{{member.name}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        memberList: [
          {
            id: 1,
            name: '陈朔'
          },
          {
            id: 2,
            name: '张三'
          },
          {
            id: 3,
            name: '李四'
          },
          {
            id: 4,
            name: '王五'
          },
          {
            id: 5,
            name: '王五A'
          },
          {
            id: 6,
            name: '王五B'
          },
          {
            id: 7,
            name: '王五C'
          },
          {
            id: 8,
            name: '王五D'
          }
        ],
        memberSelected: []
      }
    },
    methods: {
      select (index) {
        let name = ''
        for (let i in this.memberList) {
          if (this.memberList[i].id === index) {
            name = this.memberList[i].name
          }
        }

        console.log(index)

        for (let i in this.memberSelected) {
          if (this.memberSelected[i].id === index) {
            console.log(i)
            this.memberSelected.splice(i, 1)
            return true
          }
        }

        this.memberSelected.push({
          id: index,
          name: name
        })
      }
    }
  }
</script>

<style>
  .member-select {
    width: 100%;
    display: flex;
  }
  .member-list {
    width: 50%;
  }
  .member-select {
    width: 50%;
  }
</style>




